.fb-dual-button-container {
    .fb-advanced-button {
        align-items: center;
        display: inline-flex;
        justify-content: center;
        outline: none;
        text-align: center;
        text-decoration: none;
        padding: 12px 30px;
        background-color: #3f8efc;
        border-radius: 0px;
        box-sizing: border-box;
        cursor: pointer;
        position: relative;
        transition: all .3s ease-in-out;
        height: 100%;
        white-space: nowrap;
        .fb-advanced-button-text {
            color: #ffffff;
            transition: all .3s ease-in-out;
        }
    }
	.fb-dual-button-wrapper {
		display: flex;
	}
	.fb-dual-button-connector{
		position: relative;
	}
	.fb-dual-button-connector-text{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 99;
		border-radius: 50px;
		text-align: center;
        width: 35px;
        height: 35px;
        line-height: 35px;
	}
    @media all and (max-width: 1024px) {
        .fb-dual-button-connector-text{
            width: fit-content;
            line-height: 1.3;
            height: 15px;
        }
    }
    @media all and (max-width: 767px) {
        .fb-dual-button-connector-text{
            width: fit-content;
            line-height: 1.3;
            height: 15px;
        }
    }
    .fb-advanced-button-icon {
        display: block;
        position: relative;
        z-index: 1;
        svg {
            display: block;
            height: auto;
            width: 16px;
            fill: #fff;
            box-sizing: content-box;
        }
    }
}
